<template lang="html">
    <div class="quest-fild">
        <h4>Покажи свои знание и узнаешь кто ты</h4>
        <hr>
        <a @click="$emit('onStart')" class="waves-effect waves-light btn">Начать</a>
    </div>
</template>

<script>
export default {

}
</script>

<style lang="css">
html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background: url('../img/6517683-thomas-kinkade-summer-wallpaper.jpg') no-repeat;
    background-size: cover;
    position: relative;
}
hr {
    margin: 20px 0;
}
h1 {
    text-align: center;
    color: white;
}
h2 {
    margin-top: 0;
}
.quest-fild {
    background: rgba(203, 203, 203, 0.81);
    color: white;
    text-align: center;
    padding: 30px;
    width: 600px;
    margin: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.wariants {
    margin: 20px 0;
}
</style>



