<template>
    <div>
        <AppStartScreen v-if="state == 'start'" @onStart="onStart"></AppStartScreen>
        <AppQuestion v-else-if="state == 'question'" @onResult="onResult"></AppQuestion>
        <AppResultScreen :result='result' v-else-if="state == 'result'"></AppResultScreen>
    </div>
</template>

<script>
export default {
    data () {
        return {
            state: 'start',
            result: ''
        }
    },
    methods: {
        onResult(score) {
            this.state = 'result';
            this.result = score;
            console.log(score);
        },
        onStart() {
            this.state = 'question'
        },

    }
}
</script>

<style lang="css">
</style>



