<template lang="html">
    <div class="res">
        <h1>Ваш результат</h1>

        <div v-if="result <= 2">
            <div class="resul"> {{result}} из 15</div>
            <div>Вы - <span class="cool">Деревенский дурачок</span></div>
            <img src='../img/dev.jpg' alt="">
        </div>

        <div v-else-if="3 <= result && result <= 6">
            <div class="resul"> {{result}} из 15</div>
            <div>Вы - <span class="cool">Монах из героев</span></div>
            <img src='../img/monach.jpg' alt="">
        </div>

        <div v-else-if="7 <= result && result <= 10">
            <div class="resul"> {{result}} из 15</div>
            <div>Вы - <span class="cool">Няшный ангел</span></div>
            <img src='../img/niash.jpg' alt="">
        </div>

        <div v-else-if="11 <= result && result <= 13">
            <div class="resul"> {{result}} из 15</div>
            <div>Вы - <span class="cool">Боевой Архангел</span></div>
            <img src='../img/arch.jpg' alt="">
        </div>

        <div v-else>
            <div class="resul"> {{result}} из 15</div>
            <div>Вы - <span class="cool">Мистер Манхеттен</span></div>
            <img src='../img/manhetten.jpg' alt="">
        </div>

    </div>

</template>

<script>
export default {
  data() {
    return {
      res: picture
    }
  },
  props: ['result'],
}

</script>

<style lang="css">
.res {
    text-align: center;
    color: white;
    margin: 0 20px;
}
.res img {
    max-width: 600px;
    margin-top: 20px;
}
.resul {
    font-size: 22px;
    font-weight: 700;
    margin: 10px 0;
}
.cool {
    font-size: 30px;
    text-transform: uppercase;
    color: red;
    font-weight: 700;
    text-shadow: -1px 0 1px white, 0 -1px 1px white, 0 1px 1px white, 1px 0 1px white, 0 0 8px white, 0 0 8px white, 0 0 8px white, 2px 2px 3px black;
}
</style>



