<template lang="html">
    <div class="quest-fild">
        <h2>Выберите ответ</h2>
        <hr>
        <div class="main" v-for="(item, index) in question">
            <div v-show="index === questionIndex">
                <div class="quest">{{item.text}}</div>
                <div class="wariants">
                    <a class="waves-effect waves-light btn" @click="next(wars.data, $event)" v-for="wars in item.answers">{{wars.text}}</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            question: quest,
            questionIndex: 0,
            score: 0
        }
    },
    methods: {
        next(data, e) {
            this.questionIndex ++;
            this.score += data;
            if(this.questionIndex == 15 || this.question.length == this.questionIndex) {
                this.$emit('onResult', this.score)
            }
        }
    }
}

let quest = [
    {text: 'Кто был первым царём Израиля??', answers: [
        {text: 'Давид', data: 0},
        {text: 'Саул', data: 1},
        {text: 'Соломон', data: 0},
        {text: 'Голиаф', data: 0},
    ]},
    {text: 'Назовите город, в котором родился Иисус Христос', answers: [
        {text: 'Вифлеем', data: 1},
        {text: 'Иерусалим', data: 0},
        {text: 'Галилея', data: 0},
        {text: 'Назарет', data: 0},
    ]},
    {text: 'Название моря, по которому ходил Христос', answers: [
        {text: 'Галилейское море', data: 1},
        {text: 'Мёртвое море', data: 0},
        {text: 'Красное море', data: 0},
        {text: 'Средиземное море', data: 0},
    ]},
    {text: 'Имя первого мученика за веру', answers: [
        {text: 'Петр', data: 0},
        {text: 'Варнава', data: 0},
        {text: 'Стефан', data: 1},
        {text: 'Иоанн', data: 0},
    ]},
    {text: 'Каким ремеслом занимался апостол Павел во время миссионерских путешествий?', answers: [
        {text: 'пас овец', data: 0},
        {text: 'был моряком', data: 0},
        {text: 'шил палатки', data: 1},
        {text: 'ловил рыбу', data: 0},
    ]},
    {text: 'Как звали жену Моисея?', answers: [
        {text: 'Мариам', data: 0},
        {text: 'Елисавета', data: 0},
        {text: 'Ревекка', data: 0},
        {text: 'Сепфора', data: 1},
    ]},
    {text: 'Назовите имя ангела, восставшего против Бога', answers: [
        {text: 'Гавриил', data: 1},
        {text: 'Серафим', data: 0},
        {text: 'Михаил', data: 0},
        {text: 'Сын зари', data: 0},
    ]},
    {text: 'Как звали первенца Адама и Евы?', answers: [
        {text: 'Авель', data: 0},
        {text: 'Ламех', data: 0},
        {text: 'Каин', data: 1},
        {text: 'Сиф', data: 0},
    ]},
    {text: 'Что в дар Богу принёс Авель?', answers: [
        {text: 'плоды земли', data: 0},
        {text: 'животное', data: 1},
        {text: 'драгоценности', data: 0},
        {text: 'елей', data: 0},
    ]},
    {text: 'Какой дар принес Каин Богу?', answers: [
        {text: 'плоды земли', data: 1},
        {text: 'животное', data: 0},
        {text: 'драгоценности', data: 0},
        {text: 'елей', data: 0},
    ]},
    {text: 'Как звали сына Адама и Евы, родившегося после смерти Авеля?', answers: [
        {text: 'Сиф', data: 1},
        {text: 'Ламех', data: 0},
        {text: 'Мафусал', data: 0},
        {text: 'Енох', data: 0},
    ]},
    {text: 'Какой пророк вознёсся на Небо на огненной колеснице?', answers: [
        {text: 'Моисей', data: 0},
        {text: 'Илия', data: 1},
        {text: 'Исаия', data: 0},
        {text: 'Елисей', data: 0},
    ]},
    {text: 'По чьему приказу Солнце и Луна остановились?', answers: [
        {text: 'Иисуса Навина', data: 1},
        {text: 'Аарона', data: 0},
        {text: 'Халева', data: 0},
        {text: 'Моисея', data: 0},
    ]},
    {text: 'Представители какой религиозной группы не верили в воскресение из мертвых?', answers: [
        {text: 'Саддукеи', data: 1},
        {text: 'Фарисеи', data: 0},
        {text: 'Книжники', data: 0},
    ]},
    {text: 'Какое животное привёл Бог в ответе на вопрошание Иова?', answers: [
        {text: 'Бегемота', data: 1},
        {text: 'Орла', data: 0},
        {text: 'Льва', data: 0},
        {text: 'Черепаху', data: 0},
    ]},
    {text: 'Какое животное привёл Бог в ответе на вопрошание Иова?', answers: [
        {text: 'Бегемота', data: 1},
        {text: 'Орла', data: 0},
        {text: 'Льва', data: 0},
        {text: 'Черепаху', data: 0},
    ]},
    {text: 'Кто был рожден от Непорочного зачатия по католической доктрине?', answers: [
        {text: 'Иисус Христос', data: 0},
        {text: 'Дева Мария', data: 1},
        {text: 'Моисей', data: 0},
        {text: 'Иуда', data: 0},
    ]},
    {text: 'Сколько овец взял Ной на ковчег?', answers: [
        {text: '1', data: 0},
        {text: '14', data: 1},
        {text: '2', data: 0},
        {text: '3', data: 0},
    ]},
    {text: 'Сколько ангелов было у гроба господня?', answers: [
        {text: '1', data: 0},
        {text: '2', data: 1},
        {text: '3', data: 0},
        {text: 'от 1 до 3', data: 0},
    ]},
    {text: 'Какое евангелие было дописано после смерти евангелиста?', answers: [
        {text: 'от Иоана', data: 0},
        {text: 'от Марка', data: 1},
        {text: 'от Матфея', data: 0},
        {text: 'от Луки', data: 0},
    ]},
];

    for (var i = 0; i < quest.length; i++) {
        quest[i].answers.sort(function(){ return 0.5-Math.random() })
    }

    quest.sort(function(){ return 0.5-Math.random() })

</script>

<style lang="css">
.wariants {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
}
.wariants a {
    display: block;
    margin: 0 10px;
}
.quest {
    font-size: 20px
}
</style>



